.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.label {
  display: flex;
  flex-direction: column;
  gap: 8px;

  font-size: 28px;
  text-align: center;

  user-select: none;
}

.label:last-of-type {
  margin-bottom: 20px;
}

.name,
.number {
  height: 32px;
  border-radius: 20px;
  border: none;

  padding-left: 14px;
  font-size: 20px;
  font-weight: 600;

  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.submitButton {
  font-size: 24px;
  color: inherit;
  text-transform: capitalize;

  width: 200px;

  margin: 0 auto;
  padding: 12px 24px;

  background-color: teal;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);

  user-select: none;
  cursor: pointer;

  transition: background-color 250ms ease;
}

.submitButton:hover {
  background-color: rgb(1, 100, 100);
}
